import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

import About from './views/about/About.vue'
import Contact from './views/about/Contact.vue'
import Locations from './views/about/Locations.vue'

import DecoratingProducts from './views/products/DecoratingProducts.vue'
import FlooringProducts from './views/products/FlooringProducts.vue'
import PaintProducts from './views/products/PaintProducts.vue'

import DecoratingServices from './views/services/DecoratingServices.vue'
import FlooringServices from './views/services/FlooringServices.vue'
import PaintServices from './views/services/PaintServices.vue'

import Blog from './views/Blog.vue'
import ProShop from './views/ProShop.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/contact-us',
      name: 'contact us',
      component: Contact
    },
    {
      path: '/locations',
      name: 'locations',
      component: Locations
    },
    {
      path: '/products/decorating',
      name: 'decorating products',
      component: DecoratingProducts
    },
    {
      path: '/products/flooring',
      name: 'flooring products',
      component: FlooringProducts
    },
    {
      path: '/products/paint',
      name: 'paint products',
      component: PaintProducts
    },
    {
      path: '/services/decorating',
      name: 'decorating services',
      component: DecoratingServices
    },
    {
      path: '/services/flooring',
      name: 'flooring services',
      component: FlooringServices
    },
    {
      path: '/services/paint',
      name: 'paint services',
      component: PaintServices
    },
    {
      path: '/blog',
      name: 'blog',
      component: Blog
    },
    {
      path: '/contractor-pro-shop',
      name: 'contractor pro shop',
      component: ProShop
    }
  ]
})
